<template>
	<div class="history-filter">
		<Button v-if="dataReady" @click="showModal = true" class="history-filter__item history-filter__item--width-all">
			<span>Все</span>
			<svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" width="14" height="14" viewBox="0 0 14 14" fill="none">
				<mask id="mask0_759_2847" style="mask-type:alpha" maskUnits="userSpaceOnUse" x="0" y="0" width="14" height="14">
					<rect x="14" width="14" height="14" transform="rotate(90 14 0)" fill="url(#pattern0)"/>
				</mask>
				<g mask="url(#mask0_759_2847)">
					<rect x="14.875" width="14" height="14.875" transform="rotate(90 14.875 0)" fill="#808080"/>
				</g>
				<defs>
					<pattern id="pattern0" patternContentUnits="objectBoundingBox" width="1" height="1">
						<use xlink:href="#image0_759_2847" transform="scale(0.00195312)"/>
					</pattern>
					<image id="image0_759_2847" width="512" height="512" xlink:href="data:image/png;base64,"/>
				</defs>
			</svg>
		</Button>
		<Wave v-else class="history-filter__item history-filter__item--width-all"/>

		<template v-for="filter in filters" :key="filter">
			<div v-if="dataReady" class="history-filter__item" :class="`history-filter__item--width-${filter}`">
				<div class="history-filter__item-icon"></div>
				{{items[filter]}}
			</div>
			<Wave v-else class="history-filter__item history-filter__item--width-income"/>
		</template>
	</div>
	<Teleport to="body">
		<Modal :show="showModal" @close="showModal = false">
			<template #header>
				<div class="history-filter__modal-header">
					<div class="history-filter__modal-title">Тип операции</div>
				</div>
			</template>
			<template #body>
				<ul class="history-filter__modal-body">
					<li><input type="checkbox" @click="checkedAll" v-model="all">Все</li>
					<template v-for="item in Object.keys(items)" :key="item">
						<li><input type="checkbox" :value="item" v-model="modalFilters">{{items[item]}}</li>
					</template>
				</ul>
			</template>
			<template #footer>
				<Button class="history-filter__modal-btn"
						@click="apply"
				>Применить
				</Button>
			</template>
		</Modal>
	</Teleport>
</template>

<script setup>
import Button from '@/shared/components/Button';
import Wave from '@/shared/components/Preloaders/Wave';
import Modal from '@/shared/components/Modal';
import {
	defineProps, defineEmits, ref, watch, onMounted,
} from 'vue';

const props = defineProps(['dataReady', 'filters']);
const emit = defineEmits(['editFilter']);
const showModal = ref(false);
const all = ref(false);
const items = {
	income: 'Пополнения',
	expense: 'Расходы',
	unfinished: 'Незавершенные операции',
};

const modalFilters = ref([]);
onMounted(() => {
	modalFilters.value = props.filters;
});
const checkedAll = () => {
	modalFilters.value = all.value ? [] : Object.keys(items);
};
watch(modalFilters, () => {
	if (modalFilters.value.length < Object.keys(items).length) {
		all.value = false;
	}
	if (modalFilters.value.length === Object.keys(items).length) {
		all.value = true;
	}
});
const apply = () => {
	if (modalFilters.value.length) {
		emit('editFilter', modalFilters.value);
		showModal.value = false;
	} else {
		// eslint-disable-next-line no-alert
		alert('Выберите фильтр');
	}
};
</script>

<style scoped lang="scss">
@import 'historyFilter';
</style>
