<template>
	<MainSkeleton :beforeDate="beforeDate"
					:time="contract?.updated_at.slice(11, 16)" :dataReady="dataReady">
		<template v-slot:amountToBeRefunded>
			<AmountToBeRefunded  :sum="contract?.total_debt" :dataReady="dataReady"/>
		</template>
		<template v-slot:message>
			<MessageMain :dataReady="dataReady">
				До окончания действия договора
				осталось 10 дней
			</MessageMain>
		</template>
		<template v-slot:mainFunc>
			<ButtonMainFunc :dataReady="dataReady" :onClick="()=>router.push('/payment')" class="button-main-func--dark">Оплатить</ButtonMainFunc>
			<ButtonMainFunc :dataReady="dataReady" :onClick="()=>router.push('/contract-info')" class="button-main-func--light">Мой договор</ButtonMainFunc>
			<ButtonMainFunc :dataReady="dataReady" :onClick="click" class="button-main-func--light">Ещё</ButtonMainFunc>
		</template>
	</MainSkeleton>
</template>

<script setup>

import MainSkeleton from '@/entities/components/MainSkeleton';
import AmountToBeRefunded from '@/entities/components/AmountToBeRefunded';
import ButtonMainFunc from '@/entities/components/ButtonMainFunc';
import MessageMain from '@/entities/components/MessageMain';
import { computed, ref } from 'vue';
import { useStore } from 'vuex';
import { useRouter } from 'vue-router';

const store = useStore();
const router = useRouter();

const dataReady = ref(true);
const contract = computed(() => store.state.contract);
const beforeDate = computed(() => new Date(contract?.value?.datereturn).toLocaleString('ru', { year: 'numeric', month: 'long', day: 'numeric' }));
const click = () => {
	console.log('еще');
};
</script>

<style scoped>
@import "mainInfoFunc.scss";
</style>
